<template>
  <div class="report">
    <div class="frame-fun clear">
      <h3 class="module-name">
        <span>{{ moduleName }}</span>
        <router-link
          class="el-button el-button--primary"
          :to="`/module-view/${moduleid}/report`"
        >新建报表</router-link>
      </h3>
      <el-form :inline="true">
        <el-form-item label="决策时间">
          <datepicker
            :init-dates.sync="decisionDates"
            v-on:update:date="(newV) => decisionDates = newV"
          ></datepicker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.stop="searchFunc">查询</el-button>
        </el-form-item>
        <br>
        <el-form-item>
          <el-checkbox
            v-model="checked"
            :indeterminate="isIndeterminate"
            @change="handleCheckAllChange"
          >{{checked?'全不选':'全选'}}</el-checkbox>
        </el-form-item>
      </el-form>
    </div>
    <div class="panel-body">
      <report
        v-for="(item, index) in curReportList"
        :key="index"
        :report="item"
        :index="index"
        :module-name="moduleName"
        v-on:refresh="getReportList"
        :ref="`report${index}`"
      ></report>
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import datepicker from '@/components/datepicker';
import {getReportListById} from '@/api/monitor.js';
import report from '@/components/report.vue';
export default {
  name: 'page-report',
  props: ['moduleid'],
  components: {
    report,
    datepicker
  },
  data() {
    return {
      decisionDates: [
        dayjs()
          .add(-3, 'month')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD')
      ],
      reportList: [],
      curReportList: [],
      checked: false,
      isIndeterminate: false
    };
  },
  computed: {
    moduleName() {
      let name = '';
      for (let i = 0; i < this.reportList.length; i++) {
        let productName = this.reportList[i].productName;
        if (productName) {
          name = productName;
          break;
        }
      }
      return name;
    }
  },
  watch: {
    curReportList: {
      handler: function(newV) {
        if (Array.isArray(newV)) {
          let checkedArr = this.curReportList.filter(item => item.checked === true);
          if (checkedArr.length === 0) {
            this.checked = false;
            this.isIndeterminate = false;
          } else if (checkedArr.length === this.curReportList.length) {
            this.checked = true;
            this.isIndeterminate = false;
          } else {
            this.isIndeterminate = true;
          }
        }
      },
      deep: true,
      immediate: true
    }
  },
  created() {
    if (!this.moduleid) {
      //TODO 不存在该模块，提示错误
    }
    this.reportid = this.$route.query.reportid;
    const start = this.$route.query.starttime;
    const end = this.$route.query.endtime;
    const date = this.$route.query.starttime;
    if (start && end) {
      this.decisionDates = [start, end];
    }
    this.getReportList();
  },
  mounted() {},
  methods: {
    getReportList() {
      getReportListById(this.moduleid)
        .then(result => {
          const data = result.result;
          if (Array.isArray(data)) {
            this.reportList = data;
            if (this.reportid) {
              this.reportList = this.reportList.filter(item => item.id === this.reportid);
              console.log(this.reportList);
            }
            for (let item of this.reportList) {
              this.$set(item, 'checked', false);
            }
            this.curReportList = this.reportList;
            return;
          }
          this.curReportList = this.reportList = [];
        })
        .catch(err => {
          console.log(err);
          this.curReportList = this.reportList = [];
        });
    },
    searchFunc() {
      if (!this.decisionDates[0]) {
        this.$message.warning('请选择决策起始时间');
        return;
      }
      if (!this.decisionDates[1]) {
        this.$message.warning('请选择决策截止时间');
        return;
      }
      if (!this.curReportList.some(item => item.checked === true)) {
        this.$message.warning('请选择需要查询的报表');
        return;
      }
      for (let i = 0; i < this.curReportList.length; i++) {
        if (this.curReportList[i].checked) {
          let name = `report${i}`;
          this.$refs[name][0].$emit('search', this.decisionDates[0], this.decisionDates[1]);
        }
      }
    },
    handleCheckAllChange() {
      this.curReportList.forEach(item => (item.checked = this.checked));
    }
  }
};
</script>

<style scoped>
.report {
  padding: 30px 20px;
}
.module-name {
  display: block;
  margin: 10px 50px 0 20px;
  font-family: PingFangSC-Semibold;
  font-size: 18px;
  color: #78818f;
  letter-spacing: 0;
  margin-bottom: 30px;
}
.module-name span {
  margin-right: 20px;
}
.el-input {
  width: 220px;
}
.el-form {
  padding-left: 20px;
}
</style>
