<template>
  <div class="ibox">
    <div class="ibox-title" @click.stop="isClosed = !isClosed">
      <h5>
        <el-checkbox v-model="report.checked" style="margin-right: 10px;"></el-checkbox>
        {{name}}
      </h5>
      <div>
        <router-link
          class="el-button el-button--text"
          :to="`/module-view/${report.productid}/report-view/${report.id}`"
          tag="p"
        >配置</router-link>
        <span @click.stop.prevent="showConfirmFunc">
          <img src="../assets/close-btn-1.png" alt="删除">
        </span>
        <span>
          <img src="../assets/down.png" v-if="isClosed" alt="收起">
          <img src="../assets/up.png" v-else alt="展开">
        </span>
      </div>
    </div>
    <transition name="fade">
      <div class="ibox-content" v-show="!isClosed">
        <div class="wrap-table-comm" v-if="searched">
          <table
            class="table-comm"
            v-if="report.reportType=='SingleAndDate' || report.reportType=='MultivariateAndDate'"
          >
            <thead v-if="reportData.head.content.length">
              <tr>
                <th colspan="2">{{cellTitle}}</th>
                <th v-for="(item, index) in reportData.head.content" :key="index">{{item}}</th>
              </tr>
            </thead>
            <tbody v-if="!reportData.body.content.length">
              <tr>
                <td :colspan="reportData.head.content+2">暂无数据</td>
              </tr>
            </tbody>
            <tbody v-if="reportData.body.content.length">
              <tr v-for="(item, index) in reportData.body.content" :key="index">
                <td
                  :rowspan="reportData.body.content.length"
                  v-if="index==0"
                >{{reportData.body.title}}</td>
                <td
                  v-for="(one, i) in item"
                  :key="i"
                  :class="{warning: one.abnormal}"
                >{{one.value || '-'}}</td>
              </tr>
            </tbody>
          </table>
          <table class="table-comm" v-if="report.reportType=='Multivariate'">
            <thead v-if="reportData.head.content.length">
              <tr>
                <th colspan="2" rowspan="2">{{cellTitle}}</th>
                <th
                  :colspan="reportData.head.content.length"
                  v-if="reportData.head.content.length"
                >{{reportData.head.title}}</th>
              </tr>
              <tr>
                <th v-for="(item, index) in reportData.head.content" :key="index">{{item}}</th>
              </tr>
            </thead>
            <tbody v-if="!reportData.body.content.length">
              <tr>
                <td :colspan="reportData.head.content.length+2">暂无数据</td>
              </tr>
            </tbody>
            <tbody v-if="reportData.body.content.length">
              <tr v-for="(item, index) in reportData.body.content" :key="index">
                <td
                  :rowspan="reportData.body.content.length"
                  v-if="index==0"
                >{{reportData.body.title}}</td>
                <td
                  v-for="(one, i) in item"
                  :key="i"
                  :class="{warning: one.abnormal}"
                >{{one.value || '-'}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </transition>
    <dialog-confirm @confirm="delModuleFunc" :init-visible.sync="showDialogConfirm"></dialog-confirm>
  </div>
</template>

<script>
import dialogConfirm from '@/components/dialog-confirm';
import {getVarList} from '@/api/sample.js';
import {getReportData, delReport} from '@/api/monitor.js';
import {getOverdueList} from '@/assets/scripts/getSomeSetting.js';

export default {
  name: 'report',
  components: {
    dialogConfirm
  },
  props: {
    report: {
      type: Object,
      default: () => {
        return {
          name: '',
          id: '',
          checked: false,
          reportType: ''
        };
      }
    },
    index: {
      type: Number
    },
    moduleid: {
      type: String
    },
    refresh: {
      type: Function
    }
  },
  data() {
    return {
      searched: false,
      isClosed: true,
      overduelist: getOverdueList(),
      date: '',
      cellTitle: '',
      tableTitle: '',
      showDialogConfirm: false,
      reportData: {
        head: {
          title: '',
          content: []
        },
        body: {
          title: '',
          content: []
        }
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now() || time.getTime() < new Date('2015/07/01').getTime();
        }
      }
    };
  },
  computed: {
    name() {
      return `${this.index + 1}. ${this.report.name}`;
    }
  },
  created() {
    const start = this.$route.query.starttime;
    const end = this.$route.query.endtime;
    const date = this.$route.query.starttime;
    if (this.report.reportType === 'Multivariate' && date) {
      this.date = date;
      this.isClosed = false;
      this.searchFunc();
    }
    if (
      (this.report.reportType == 'SingleAndDate' || this.report.reportType == 'MultivariateAndDate') &&
      start &&
      end
    ) {
      this.report.checked = true;
      this.isClosed = false;
      this.searchFunc(start, end);
    }
    this.$on('search', (starttime, endtime) => {
      this.searchFunc(starttime, endtime);
    });
    const samplename = Array.isArray(this.report.indexName) ? this.report.indexName[0] : this.report.indexName;
    getVarList(samplename)
      .then(result => {
        if (Array.isArray(result.result)) {
          if (!result.result.length) {
            return;
          }
          this.varList = result.result.map(({key, vbname}) => {
            return {
              text: vbname,
              value: key
            };
          });
          this.getCellTitle();
        }
      })
      .catch(err => console.log(err));
  },
  mounted() {},
  methods: {
    getCellTitle() {
      let subTitle = '';
      if (this.report.loanType === 'BEFORE_LOAN') {
        if (this.report.resultValueType === 'FIELD_AVG') {
          let theOne = this.varList.find(item => item.value === this.report.resultField) || {};
          subTitle = `贷前_平均值_${theOne.text}`;
        } else {
          subTitle = `贷前_计数`;
        }
        subTitle = this.resultValueType ? `${subTitle}（%）` : subTitle;
      } else if (this.report.loanType === 'AFTER_LOAN') {
        let theOne = this.overduelist.find(item => item.value === this.overdue) || {};
        subTitle = `贷后_${theOne.text}_${this.report.resultValueType === 'COUNT' ? '人数' : '金额'}_${
          this.report.resultNeedPercent === 'rate' ? '逾期率（%）' : '逾期量'
        }`;
      }
      this.cellTitle = subTitle;
    },
    searchFunc(starttime, endtime) {
      this.isClosed = false;
      getReportData(this.report.id, starttime, endtime)
        .then(result => {
          if (!this.searched) {
            this.searched = true;
          }
          this.dealWithData(result.result.viewReport);
        })
        .catch(err => {
          console.log(err);
          if (!this.searched) {
            this.searched = true;
          }
          this.resetRepostData();
        });
    },
    // 处理后端返回的数据，并添加总计栏
    dealWithData(data = {}) {
      this.reportData.head.title = data.headTitle;
      this.reportData.body.title = data.bodyTitle;
      if (Array.isArray(data.head)) {
        this.reportData.head.content = data.head;
      }
      if (Array.isArray(data.body)) {
        this.reportData.body.content = data.body;
        for (let item of this.reportData.body.content) {
          let num = 0;
          for (let one of item) {
            let val = one.value;
            if (!isNaN(parseFloat(val))) {
              num += parseFloat(val);
            }
          }
        }
      }
    },
    resetRepostData() {
      this.reportData = {
        head: {
          title: '',
          content: []
        },
        body: {
          title: '',
          content: []
        }
      };
    },
    showConfirmFunc() {
      this.showDialogConfirm = true;
    },
    delModuleFunc() {
      this.showDialogConfirm = false;
      delReport(this.report.id)
        .then(result => {
          this.$message.success('删除成功');
          this.$emit('refresh');
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

